import React from 'react'
import PropTypes from 'prop-types'
import colors from 'styles/colors'
import LayoutMenu from 'app/Header'

const styles = {
  main: {
    zIndex: 999,
    display: 'flex',
    flexDirection: 'column',
    minHeight: '100vh',
  },
  menu: {
    display: 'flex',
    alignItems: 'center',
    background: colors.white,
    height: 80,
  },
  content: {
    display: 'flex',
    flexDirection: 'rows',
  },
  pageSpace: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    background: colors.greyBg,
    height: `calc(100vh - 80px)`,
    width: '100%',
  },
  sideMenu: {
    background: colors.white,
    minWidth: 120,
    boxShadow: '5px 4px 14px rgba(0, 0, 0, 0.04)',
  },
}

const BaseLayout = ({ children, sideMenu }) => {
  const Menu = sideMenu
  return (
    <div style={styles.main}>
      <div style={styles.menu}>
        <LayoutMenu />
      </div>
      <div style={styles.content}>
        {sideMenu && (
          <div style={styles.sideMenu}>
            <Menu />
          </div>
        )}
        <div style={styles.pageSpace}>{children}</div>
      </div>
    </div>
  )
}

BaseLayout.propTypes = {
  children: PropTypes.node,
  sideMenu: PropTypes.node,
}

export default BaseLayout
