import React from 'react'
import BaseLayout from 'layouts/BaseLayout'

const style = {
  error404: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  title: {},
  goBack: {},
}

const NotFoundPage = () => (
  <BaseLayout>
    <div style={style.error404}>
      <h1 style={style.title}>404: Page not found.</h1>
      <div>
        <span>Go back: </span>
        <span>
          <a style={style.goBack} href='/'>
            Home Page
          </a>
        </span>
      </div>
    </div>
  </BaseLayout>
)

export default NotFoundPage
